import axios from 'axios';
import { apiEndpoint } from './parameters';

export const downloadResume = (path) => {
  return async () => {
    try{
      const response = await axios.post(`${apiEndpoint}/download-link`, {
        path
      });
      window.open(response.data.signedUrl)
    }catch(error) {
      /*eslint no-console:0*/
      console.error(error)
    }
  }
};
